<script>
export default {
  name: "RatingSection",
  data() {
    return {
      bookRating: null,
      ratePermission: true,
    };
  },
  methods: {
    created() {
      this.ratePermission = true;
    },
    rateBook({ target }) {
      this.bookRating = +target.value;
      this.$emit("rateBook", this.bookRating);
      this.ratePermission = false;
    },
  },
};
</script>
